'use client';
import React, {useEffect, useState } from "react";
import Navbar from "@/components/Navbar";
import Profile from "@/components/Profile";
import axios from "axios";


const domainProfiles = [
  "Marketing", "Engineering", "3D Printing", "ASP.NET Development", "Accounts",
  "Acting", "Aerospace Engineering", "Agriculture & Food Engineering", "Analytics",
  "Anchoring", "Android App Development", "Angular.js Development", "Animation",
  "Architecture", "Artificial Intelligence (AI)", "Audio Making/Editing", "Auditing",
  "Automobile Engineering", "Backend Development", "Bank", "Big Data", "Bioinformatics",
  "Biology", "Biotechnology Engineering", "Blockchain Development", "Blogging",
  "Brand Management", "Business Development", "Business/MBA", "CA Articleship"
];

const InternshipFilters = () => {
  const [filters, setFilters] = useState({
     domain_profile: "",
    location: "",
    workFromHome: false,
    partTime: false,
    stipend: 0,
    startDate: "",
    duration: "",
    jobOffer: false,
  });
  const [internships, setInternships] = useState([]);
  const [showProfile, setShowProfile] = useState(false);
  const onClick = () => {
    setShowProfile((prev) => !prev);
  };

  const handleFilterChange = (e) => {
    const { name, value, type, checked } = e.target;
    setFilters({
      ...filters,
      [name]: type === "checkbox" ? checked : value,
    });
  };
  useEffect(() => {
    fetchInternships();
  }, [filters]);

   // Fetch internships with filters
   const fetchInternships = async () => {
    try {
      const response = await axios.get("http://localhost:5000/api/internships", {
        params: filters,
      });
      setInternships(response.data);
    } catch (error) {
      console.error("Error fetching internships:", error);
    }
  };

 
  const applyFilters = async () => {
    console.log("Applying filters:", filters); // ✅ Debugging log
    
    try {
      console.log("Sending request to:", "http://localhost:5000/api/internships"); // ✅ Debugging log
      const response = await axios.get("http://localhost:5000/api/internships", {
        params: filters,
      });
  
      console.log("API Response:", response.data); // ✅ Debugging log
  
      if (response.status === 200) {
        setInternships(response.data);
      } else {
        console.error("Unexpected response:", response);
      }
    } catch (error) {
      console.error("❌ Error fetching internships:", error);
    }
  };
  

  
  return (
<>
     {showProfile ? <Profile setShowProfile={setShowProfile} /> : null}
          <Navbar showProfile={showProfile} setShowProfile={setShowProfile} />
          <div> 
    <div className=" w-full bg-white p-4 shadow-lg flex justify-between items-center fixed top-0 left-0  mt-20">
      <h2 className="text-xl font-bold mb-4">Filter Internships</h2>
      
      {/* Profile Role */}
      <label className="block mb-2    space-y-20">Profile Role</label>
      <select
    name="domain_profile"
    value={filters.domain_profile}
    onChange={handleFilterChange}
    className="w-full p-2 border rounded"
  >
    <option value="">Select Profile</option>
    {domainProfiles.map((profile) => (
      <option key={profile} value={profile.toLowerCase().replace(/ /g, "%20")}>
        {profile}
      </option>
    ))}
  </select>

      {/* Location */}
      <label className="block mt-4 mb-2">Location</label>
      

      {/* Work from Home */}
      <div className="flex items-center mt-4">
        <input
          type="checkbox"
          name="workFromHome"
          checked={filters.workFromHome}
          onChange={handleFilterChange}
          className="mr-2"
        />
        <label>Work from Home</label>
      </div>

      {/* Part-time */}
      <div className="flex items-center mt-2">
        <input
          type="checkbox"
          name="partTime"
          checked={filters.partTime}
          onChange={handleFilterChange}
          className="mr-2"
        />
        <label>Part-time</label>
      </div>

      {/* Stipend */}
      <label className="block mt-4 mb-2">Desired Minimum Monthly Stipend (₹)</label>
      <input
        type="range"
        name="stipend"
        min="0"
        max="20000"
        step="500"
        value={filters.stipend}
        onChange={handleFilterChange}
        className="w-full"
      />
      <span className="text-sm">₹{filters.stipend}</span>

      {/* Start Date */}
      <label className="block mt-4 mb-2">Starting from (or after)</label>
      <input
        type="date"
        name="startDate"
        value={filters.startDate}
        onChange={handleFilterChange}
        className="w-full p-2 border rounded"
      />

      {/* Duration */}
      <label className="block mt-4 mb-2">Max. Duration (Months)</label>
      <select
        name="duration"
        value={filters.duration}
        onChange={handleFilterChange}
        className="w-full p-2 border rounded"
      >
        <option value="">Select Duration</option>
        <option value="1">1 month</option>
        <option value="2">2 months</option>
        <option value="3">3 months</option>
        <option value="6">6 months</option>
        <option value="12">12 months</option>
      </select>

      {/* Job Offer */}
      <div className="flex items-center mt-4">
        <input
          type="checkbox"
          name="jobOffer"
          checked={filters.jobOffer}
          onChange={handleFilterChange}
          className="mr-2"
        />
        <label>Internships with Job Offer</label>
        <button 
        onClick={applyFilters}
         className="w-full bg-blue-500 text-white p-2 rounded-md mt-2">
  Apply Filters
</button>
      </div>
      {/* Internship List */}
      <div className="flex justify-center">
   


    </div>
    
</div>
<div className="internship-list grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-6 p-4 max-w-7xl mt-40">
    {internships.length > 0 ? (
     
      internships.map((internship) => (
        <div
          key={internship._id}
          className="internship-card bg-white shadow-lg rounded-xl p-5 border border-gray-200"
        >
          <h3 className="text-lg font-semibold text-gray-800">{internship.domain_profile}</h3>
          <p className="text-gray-600"><strong>Location:</strong> {internship.location}</p>
          <p className="text-gray-600"><strong>Stipend:</strong> ₹{internship.stipend}</p>
          <p className="text-gray-600"><strong>Duration:</strong> {internship.duration} months</p>
          <p className="text-gray-600"><strong>Work From Home:</strong> {internship.workFromHome ? "Yes" : "No"}</p>
          <p className="text-gray-600"><strong>Part-time:</strong> {internship.partTime ? "Yes" : "No"}</p>
          <p className="text-gray-600"><strong>Job Offer:</strong> {internship.jobOffer ? "Yes" : "No"}</p>
        </div>
      ))
    ) : (
      <p className="col-span-full text-center text-gray-500">No internships found.</p>
    )}
  </div>
</div>
    </>
  );
};

export default InternshipFilters;
